import React, { useState, useEffect, useRef } from 'react'
import Container from '../components/Container'
import Button from '../components/Button'
import ButtonLink from '../components/ButtonLink'
import Title from '../components/Title'
// import { TweenMax, TimelineLite } from "gsap"
import cajaImg1 from '../resources/caja1.jpg'
import cajaImg2 from '../resources/caja2.jpg'
import cajaImg3 from '../resources/caja3.jpg'
import cajaImg4 from '../resources/caja4.jpg'
import cajaImg5 from '../resources/caja5.jpg'


import dataTest from '../data/dataTest.json'
import dataResponses from '../data/dataResponses.json'
import { arrayOf } from 'prop-types'

function Results( props ) {  

  // Handle images
  let iconObj = {"caja1": cajaImg1, "caja2": cajaImg2, "caja3": cajaImg3, "caja4": cajaImg4, "caja5": cajaImg5}

  // Handle dataTest filtering (only shows questions)
  const questions = dataTest.slice(2, dataTest.length - 2)
  const [currentQuestion, setCurrentQuestion] = useState(2)
  const [firstPercent, setFirstPercent] = useState(0)
  const [secondPercent, setSecondPercent] = useState(0)
  const [thirdPercent, setThirdPercent] = useState(0)

  const [prevInactive, setPrevInactive] = useState(true)
  const [nextInactive, setNextInactive] = useState(false)

  // Handle button click
  function handleButtonClick(button){
    // console.log("click", button)
    if (button === "Next" && (currentQuestion < dataTest.length  - 3)) {
      setCurrentQuestion(currentQuestion + 1)
    } else if (button === "Previous" && (currentQuestion > 2)) {
      setCurrentQuestion(currentQuestion - 1)
    }
  }  
      
  useEffect(() => {

    // Handle inactive next
    if (currentQuestion > dataTest.length - 4) {
      setNextInactive(true)
    } else (setNextInactive(false))

    // Handle inactive prev
    if (currentQuestion < 3) {
      setPrevInactive(true)
    } else (setPrevInactive(false))

    // Recoge la lista de items para calcular el porcentaje
    let answeredFirstOption = []
    let answeredSecondOption = []
    let answeredThirdOption = []

    // Index "real" que señala grupos verticales en las arrays
    let currentQuestionIndex = currentQuestion - 5

    // Entrar en cada array y sacar el resultado que cuadre con el index
    for (let i = 0; i < dataResponses.length; i++) {
      const group = dataResponses[i][currentQuestionIndex]
      if(group === "1") {
        answeredFirstOption.push(parseInt(group, 10))
      } else if (group === "2") {
        answeredSecondOption.push(parseInt(group, 10))
      } else if (group === "3") {
        answeredThirdOption.push(parseInt(group, 10))
      }
    }

    // console.log("First", answeredFirstOption, "length", answeredFirstOption.length)
    // console.log("Second", answeredSecondOption, "length", answeredSecondOption.length)
    // console.log("Third", answeredThirdOption, "length", answeredThirdOption.length)

    // Calcular el % de cada respuesta
    let TotalOfAnswers = answeredFirstOption.length + answeredSecondOption.length + answeredThirdOption.length

    if (currentQuestion === 2) {
      setFirstPercent(33)
      setSecondPercent(67)
    } else if (currentQuestion === 3) {
      setFirstPercent(86)
      setSecondPercent(14)
    } else if (currentQuestion === 4) {
      setFirstPercent(43)
      setSecondPercent(57)
    } else {
      setFirstPercent(Math.round(answeredFirstOption.length / TotalOfAnswers * 100))
      setSecondPercent(Math.round(answeredSecondOption.length / TotalOfAnswers * 100))
      setThirdPercent(Math.round(answeredThirdOption.length / TotalOfAnswers * 100))
    }

    console.log(currentQuestion)
    // console.log("% 1", FirstPercent, "% 2", SecondPercent, "% 3", ThirdPercent)
  }, [currentQuestion])

  return (
    <Container darkTheme={false}>
      <div 
        data-estoquecoñoes="Top content"
        className="w-full h-32 flex-0" 
      />

      <div 
        data-estoquecoñoes="Main content"
        className="z-10 flex flex-col items-center justify-center flex-1 w-full h-32"
        >
        {dataTest[currentQuestion].questions.map((item) => {
          return (
            <React.Fragment key={item.id}>
              <Title name={item.question}/>
            </React.Fragment>
          )
        })}
        <div className="text-black text-small md:text-normal">
          {firstPercent &&
            <React.Fragment>
              {firstPercent}% AGREE /&nbsp;
            </React.Fragment>
          }
          {secondPercent !==0 &&
            <React.Fragment>
              {secondPercent}% DISAGREE
            </React.Fragment>
          }
        </div>
      </div>
      
      <div 
        data-estoquecoñoes="Bottom content"
        className="flex items-center justify-center w-full h-32 px-16 flex-0"
        >
        <div className="w-1/3 text-center">
          <Button
            id="1"
            name="Previous"
            inactive={prevInactive}
            handleButtonClick={() => handleButtonClick("Previous")}
          />
        </div>
        <div className="w-1/3 text-center">
          <ButtonLink
            id="2"
            name="Home"
            href=""
            sliderButtons={false}
            handleButtonClick={() => handleButtonClick("Home")}
          />
        </div>
        <div className="w-1/3 text-center">
          <Button
            id="3"
            name="Next"
            inactive={nextInactive}
            handleButtonClick={() => handleButtonClick("Next")}
          />
        </div>
      </div>
      </Container>
  )
}

export default Results