import React, { useState, useRef, useEffect } from 'react'

import Title from '../components/Title'
import Container from '../components/Container'
import ButtonLink from '../components/ButtonLink'
import Button from '../components/Button'
import Product from '../components/Product'
import { TweenMax, TimelineLite } from "gsap"
import {ReactComponent as Logo} from '../resources/logo.svg'
import dataTest from '../data/dataTest.json'
import dataAllProducts from '../data/dataAllProducts.json'

import cajaImg1 from '../resources/caja1.jpg'
import cajaImg2 from '../resources/caja2.jpg'
import cajaImg3 from '../resources/caja3.jpg'
import cajaImg4 from '../resources/caja4.jpg'
import cajaImg5 from '../resources/caja5.jpg'

let iconObj = {"caja1": cajaImg1, "caja2": cajaImg2, "caja3": cajaImg3, "caja4": cajaImg4, "caja5": cajaImg5}

 
function Home( props ) {

  const [darkTheme, setDarkTheme] = useState(false)
  const [currentQuestion, setCurrentQuestion] = useState(0)
  const [result, setResult] = useState(false)
  const [randomResult, setRandomResult] = useState(0)

  const [answers, setAnswers] = useState([])
  const contentQuestions = useRef(null)
  const ProductTransition = useRef(null)

  useEffect(() => {
    setRandomResult(
      Math.floor(Math.random() * 5) + 1
    )
  }, [])

  // Handle button click
  function handleButtonClick(button){
    if (button === "Multimedia") {
      // Change page
    } else if (button === "Database") {
      // Change page
    } else {
      // Cycle through questions
      let currentQuestionValue = currentQuestion
      setCurrentQuestion(currentQuestionValue + 1)
      
      if (currentQuestion > 1) {
        // Create array of answers
        let currentAnswers = [...answers]
        currentAnswers.push(button)
        setAnswers(currentAnswers)
      }

      // Set dark theme on nth question
      if (currentQuestion === 1) {
        setDarkTheme(true)
      }

      // Dispach info to server on nth question
      if (currentQuestion === 21) {
        // console.log("dispaching info to database...")
        sendJSON(answers)
      } 
    }
  }

  function sendJSON(info){
    // console.log("sending", info)
    // let xhr = new XMLHttpRequest()
    // let url = "../data/dataResponses.json"

    // // open connection
    // xhr.open("POST", url, true)

    // // request header
    // xhr.setRequestHeader("Content-Type", "application/json")

    // // callback
    // xhr.onreadystatechange = function () {
    //   if (xhr.readyState === 4 && xhr.status === 200) {
    //     // success
    //     console.log("info received correctly, response text is",this.responseText)
    //     console.log("showing results...")
    //     let currentQuestionValue = currentQuestion
    //     setCurrentQuestion(currentQuestionValue + 1)
    //   }
    // }

    // // Convert json data to string
    // var data = JSON.stringify(info)

    // console.log(data)
    // // dispach data
    // xhr.send(data)

    // dummy
    // console.log("vamos a hacer como si el server hubiese recibido la request")
    // console.log("tira pa /results")

    setTimeout(() => {
      const tlToProduct = new TimelineLite()
      tlToProduct.from(contentQuestions.current, 0.5, {opacity:1})
      tlToProduct.to(contentQuestions.current, 0.5, {opacity:0})
    }, 2000);

    setTimeout(() => {
      setResult(true)
      const tlProductTransition = new TimelineLite()
      tlProductTransition.from(ProductTransition.current, 0.5, {opacity:0})
      tlProductTransition.to(ProductTransition.current, 0.5, {opacity:1})
      setCurrentQuestion(23)
    }, 4000);
  }

  console.log(currentQuestion)

  return (
    <Container darkTheme={darkTheme}>
      <div
        data-estoquecoñoes="Logo wrapper"
        className={`absolute top-0 left-0 z-0 flex items-center justify-center w-full h-full transition-colors duration-1000 ease-expo ${darkTheme === false ? "text-5C585C" : "text-0B0B0B"}`}
        style={{padding: "10vw"}}
      >
        <Logo/>
      </div>

      <div 
        data-estoquecoñoes="Top content"
        className="w-full h-32 flex-0" 
      />
      <div 
        data-estoquecoñoes="Main content"
        className="relative z-10 flex flex-col items-center justify-center flex-1 w-full h-32"
        >
          <div ref={ProductTransition} className="absolute w-full h-full">
            <div 
              data-estoquecoñoes="overlay con resultado"
              className={`absolute items-center justify-center w-full h-full select-none ${result === true ? "flex" : "hidden"}`}
              >
              <Product 
                onHome={true}
                id={dataAllProducts[randomResult].id}
                src={iconObj[dataAllProducts[randomResult].src]}
                supertitle={dataAllProducts[randomResult].supertitle}
                title={dataAllProducts[randomResult].title}
                description={dataAllProducts[randomResult].description}
                percentage={dataAllProducts[randomResult].percentage}
                statsDescription={dataAllProducts[randomResult].statsDescription}
                cta={dataAllProducts[randomResult].cta}
                darkMode={true}
              />
            </div>
          </div>
        <div ref={contentQuestions}>
          {dataTest[currentQuestion].questions.map((item) => {
            return (
              <div key={item.id} >
                <Title name={item.question}/>
              </div>
            )
          })}
        </div>
      </div>
      
      <div 
        data-estoquecoñoes="Bottom content"
        className="flex flex-col items-center justify-around w-full h-32 p-4 md:py-0 md:px-16 md:flex-row flex-0"
        >
        {dataTest[currentQuestion].responses.map((item) => {
          if(item.href) {
            return (
              <div 
                key={item.id} 
                className="w-1/3 text-center"
              >
                <ButtonLink
                  id={item.id}
                  name={item.name}
                  href={item.href}
                />
              </div>
            )
          } else {
            return (
              <div 
                key={item.id} 
                className="w-1/3 text-center"
              >
                <Button
                  id={item.id}
                  name={item.name}
                  handleButtonClick={() => handleButtonClick(item.id)}
                />
              </div>
            )
          }
        })}
      </div>
    </Container>
  )
}

export default Home